import React from 'react';
import PropTypes from 'prop-types';
import { Panel, ListItem, Button, Group, Div, Avatar, PanelHeader, Cell, ScreenSpinner } from '@vkontakte/vkui';

const Home = ({ id, go, fetchedUser, tokenUser, phoneUser }) => (
	<Panel id={id}>
		<PanelHeader>User Data</PanelHeader>
		
		{tokenUser&&
		<Group title="Access Token">
			<Cell
			>
			{`${tokenUser.access_token}`}
			
			</Cell>
		</Group>}
		
		

		<Group title="Панель">
			<Div>
				<Button size="xl" level="2" onClick={go} data-to="secret">
					Открыть
				</Button>
			</Div>
		</Group>
		</Panel>
);		
		
Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	phoneUser: PropTypes.shape({
	    phone_number: PropTypes.string,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
  }),
};


export default Home;
