import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View, ScreenSpinner } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Persik from './panels/Persik';

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedUser: null,
			phoneUser: null,
			tokenUser: null,
			popout: null,
		};
	}
	
  onClick () {
    this.setState({ popout: <ScreenSpinner /> });
    setTimeout(() => { this.setState({ popout: null }) }, 2000);
  }

  
  onClick () {
    this.setState({ popout: <ScreenSpinner /> });
    setTimeout(() => { this.setState({ popout: null }) }, 2000);
  }

	
	componentDidMount() {
		connect.subscribe((b) => {
			switch (b.detail.type) {
				case 'VKWebAppAccessTokenReceived':
					//this.setState({ fetchedUser: e.detail.data });
					this.setState({ tokenUser: b.detail.data });
					break;
				default:
					console.log(b.detail.type);
			}
		});
		connect.send("VKWebAppGetAuthToken", {"app_id": 6989387, "scope": "friends"});
	}
	

	go = (b) => {
		this.setState({ activePanel: b.currentTarget.dataset.to })
	};
	
	

	render() {
		return (
			<View activePanel={this.state.activePanel}>
				<Home id="home" tokenUser={this.state.tokenUser} go={this.go} />
				<Persik id="persik" go={this.go} />
			</View>
		);
	}
}

export default App;
